  padding: 15px;
  border-bottom: 1px solid #cccccc; }
  .policy span {
    display: table-cell;
    text-align: left; }

.benefit {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 15px; }

.policyBox {
  overflow-x: hidden; }

.policy-drawer {
  position: relative;
